@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
/* fonts */

:root {
    --outline: #ffa500;
}


/* css reset */

* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif!important;
}

/* css reset */


/* header */

#header {
    /* vars */
    --height: 65px;
    --bg-color: #202124;
    --color: #fff;
    --font-size: 1.1em;
    /* styles */
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* colors, fonts and size */
    background-color: var(--bg-color);
    height: var(--height);
    color: var(--color);
    font-size: var(--font-size);
    /* spacing */
    padding: 0 20px;
    /* positioning */
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    /* interaction */
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
#header nav {
    flex: 1 1;
    display: flex;
    justify-content: center;
}
#header #header__left {
    justify-content: left;
}
#header #header__right {
    justify-content: right;
}
.header_element {
    padding: 8px 4px;
    margin: 0 8px;
    color: var(--color);
    text-decoration: none;
    border-bottom: 3px solid transparent;
    transition: 0.3s;
}
.header_element:hover {
    border-color: var(--color);
    color: var(--color);
}

/* header */


/* elements */

input {
    width: 100%;
    border: none;
    outline: none;
    color: #000;
    padding: 12px 10px;
    border-radius: 5px;
    transition: 0.3s;
    font-size: 0.85em;
}
input:hover {
    box-shadow: none;
}
input:focus {
    box-shadow: 0 0 0 3px #ffa500;
    box-shadow: 0 0 0 3px var(--outline);
}

a {
    text-decoration: none;
}

/* elements */


/* asteroid info */

.asteroid_box {
    width: 100%;
    border: 1px solid rgba(128, 128, 128, 0.4);
    border-radius: 7px;
    padding: 15px;
}
.asteroid_box {
    margin-bottom: 20px;
}

/* asteroid info */


/* media queries */

@media screen and (max-width: 991px) {
    #header #header__right {
        display: none;
    }
    #header #header__left {
        justify-content: center;
    }
}
@media screen and (max-width: 500px) {
    #header {
        font-size: 0.94em;
        text-align: center;
    }
}

/* media queries */


/* map */

.leaflet-container {
    height: 80vh;
}

/* map */


/* sidebar */

.sidebar__link:not(:last-of-type) {
    margin-bottom: 5px;
}
.sidebar__link {
    padding: 5px;
    border-radius: 4px;
    font-size: 0.94em;
}
.sidebar__link:hover {
    background-color: rgba(160, 160, 160, 0.2);
}
.sidebar__link_highlight {
    background-color: rgba(160, 160, 160, 0.5)!important;
}

#aside {
    width: 360px;
}
#aside__fluid {
    position: -webkit-sticky;
    position: sticky;
    top: 70px;
}

/* sidebar */
